.card {
  height: 25%;
  margin: 0rem;
  width: 25%;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
}
.card > .img-container > img {
  width: 100%;
  height: 100%
}




.wrapper {
  padding-top: 50px;
  background: rgb(97, 93, 93);
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.navbar {
	background-color: #010f08;
	width: 100%;
	position: fixed; 
	color: white;
	font-size: 30px
}

.title {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  text-decoration: underline;
  color: rgb(238, 242, 245);
  margin-bottom: 20px;
  margin-top: 50px;
}

.body {
  width: 100%;
  text-align: center;
  font-size: 1rem;
  color: rgb(238, 242, 245);
  margin-bottom: 5px;
  margin-top: 5px;
}

.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
  color: rgb(rgb(11, 32, 189))
}

h1.body {
  font-size: 20px;
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

